/* Login Page New Theme */

.loginContainer {
    background-image: linear-gradient(to right, #9318c2, #bd39c4);
    height: 90.6vh;
    display: flex;
    padding-top: 15px;
    padding-left: 15px;
  }
  
  .loginleft .innerbox1 {
    /* background: url(../../../public/images/bg1.jpg) no-repeat 50% 50%; */
    padding: 0px 0px 0px 140px;
    float: right;
    position: relative;
    width: 500px;
    /* height: 500px;   */
  }
  
  .loginleft .innerbox1:before {
    content: "";
    width: 64px;
    height: 64px;
    /* background: url(../../../public/images/blue_right.svg) no-repeat 50% 50%; */
    position: absolute;
    top: -30px;
    left: -30px;
    z-index: -1;
  }
  
  .loginleft .innerbox1 .texts h5 {
    font-size: 22px;
    color: var(--white-color);
    padding: 29px 45px 10px 0px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    display: inline-block;
  }
  
  .loginleft .innerbox1 .texts span {
    font-family: "GraphikBold";
  }
  
  .loginleft .innerbox1 .texts p {
    font-size: 14px;
    color: var(--white-color);
    padding: 10px 0 20px;
    line-height: 20px;
    font-family: "GraphikLight";
  }
  
  .loginright1 {
    float: right;
    width: 50%;
    padding: 10px 0px;
    position: relative;
    z-index: -2;
    overflow: hidden;
  }
  
  .loginbox1 {
    /* height: 559px; */
    position: relative;
    padding: 32px 0;
    width: 55%;
    float: left;
    clear: both;
  }
  
  .loginbox1 .toplogo {
    margin-bottom: 18px;
  }
  
  .logininner1 {
    width: 282px;
    margin: 0 auto;
    padding: 20px 0;
    /*border: 0.5px solid #acd7f2;*/
    box-shadow: 0px 0px 1px rgb(13, 129, 227);
    -moz-box-shadow: 0px 0px 1px rgb(13, 129, 227);
    -webkit-box-shadow: 0px 0px 1px rgb(13, 129, 227);
    background-color: var(--white-color);
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 15px;
  }
  
  .logininner1:after,
  .logininner1:before {
    content: "";
    display: table;
    clear: both;
  }
  
  .logininner1 .fieldrow {
    padding: 0px 18px;
    float: left;
    width: 100%;
  }
  
  .logininner1 .fieldrow label {
    font-size: 11px;
    color: #0f0f0f;
    margin-bottom: 5px;
  }
  
  .logininner1 .fieldrow .custfield {
    height: 30px !important;
    font-size: 11px;
    color: #0f0f0f;
    border: 1px solid #cbc9c9;
    border-radius: 20px;
    -moz-border-radius: 20px;
    -webkit-border-radius: 20px;
  }
  
  .cform1 .fieldrow {
    margin: 10px 0px 5px;
  }
  
