  /* General styles for larger screens */
.index-header1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.rheader {
  display: flex;
  justify-content: flex-end;
}

.toplinks {
  display: flex;
  gap: 10px;
}

/* Mobile styles for screens 767px or smaller */
@media (max-width: 767px) {
  .rheader {
    position: absolute;
    bottom: 0;
    text-align: center;
    margin-top: 10px;
  }

  .toplinks {
    justify-content: center;
    gap: 15px;
  }
}

  
