  /** For Slider **/
  
  .index-main-content1 .commonslider {
    float: left;
    width: 100%;
    position: relative;
  }
  
  .index-main-content1 .commonslider .owl-carousel {
    display: none;
    width: 100%;
    -webkit-tap-highlight-color: transparent;
    position: relative;
  }
  
  .index-main-content1 .commonslider .owl-carousel .owl-stage {
    position: relative;
    -ms-touch-action: pan-Y;
    -moz-backface-visibility: hidden;
  }
  
  .index-main-content1 .commonslider .owl-carousel .owl-stage:after {
    content: ".";
    display: block;
    clear: both;
    visibility: hidden;
    line-height: 0;
    height: 0;
  }
  
  .index-main-content1 .commonslider .owl-carousel .owl-stage-outer {
    position: relative;
    overflow: hidden;
    -webkit-transform: translate3d(0px, 0px, 0px);
  }
  
  .index-main-content1 .commonslider .owl-carousel .owl-wrapper,
  .index-main-content1 .commonslider .owl-carousel .owl-item {
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
  }
  
  .index-main-content1 .commonslider .owl-carousel .owl-item {
    position: relative;
    min-height: 1px;
    float: left;
    -webkit-backface-visibility: hidden;
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
  }
  
  .index-main-content1 .commonslider .owl-carousel.owl-loaded {
    display: block;
  }
  
  .index-main-content1 .commonslider .owl-carousel.owl-loading {
    opacity: 0;
    display: block;
  }
  
  .index-main-content1 .commonslider .owl-carousel.owl-hidden {
    opacity: 0;
  }
  
  .index-main-content1 .commonslider .owl-carousel.owl-refresh .owl-item {
    visibility: hidden;
  }
  
  .index-main-content1 .commonslider .owl-carousel.owl-grab {
    cursor: move;
    cursor: grab;
  }
  
  .index-main-content1 .commonslider .owl-nav div {
